.container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.header{
  color: white;
  background-color: cadetblue;
  font-size: 3rem;
  margin: 2rem;
  width: 30%;
  text-align: center;
  padding: 0.5rem;
  border-radius: 5px;
  font-weight: 200;
}
.content{
  display: flex;
  width: 30%;
  justify-content: center;
  align-items: center;
}

.input{
  height: 2rem;
  width: 100%;
  border-radius: 5px;
  border-color: white;
  margin: 0.5rem;
  border-style: none;
  padding: 5px;
}

.add{
  color: white;
  background-color: green;
  border-color: green;
  border-radius: 5px;
  padding: 5px;
  font-weight: 500;
  width: 150px;
  height: 2.5rem;
  border-style: none;
  font-size: medium;
}
.list-div{
  display: flex;
  justify-content: space-between;
  padding: 10px;
  width: 100%;
  text-align: center;
  margin: 0.3rem;
  border-radius: 5px;
  color: white;
  margin: 0.5rem;
  font-size: 1rem;
  background-color:rgb(54, 53, 53);

}
.order-list{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
}
.list{
margin: 0.5rem;
padding: 0;
}

.remove-btn{
  color: gray;
  background-color: rgb(64, 59, 59);
  border-radius: 5px;
  border-style: none;
  padding: 0;
  width: 4rem;
}
.remove-all-btn{
  color: white;
  background-color: red;
  width: 10rem;
  padding: 2px;
  height: 2rem;
  border-radius: 5px;
  border-style:none ;
}